<template>
	<ul class="notice-list" id="notice-list">
		<li
			class="marB-10"
			v-for="boardItem in boardList"
			:key="boardItem._id"
			:item="boardItem"
		>
			<router-link
				class="flex "
				:to="{ name: `${type}Detail`, params: { id: boardItem.id } }"
			>
				<span class="body3">{{ boardItem.title }}</span>
				<h6 class="gray">{{ boardItem.date }}</h6>
			</router-link>
		</li>
	</ul>
</template>

<script>
export default {
	props: {
		boardList: {
			type: Array,
			required: true,
		},
		type: {
			type: String,
			required: true,
		},
	},
	methods: {
		goto(url) {
			this.$router.push(url);
		},
	},
};
</script>

