<template>
	<section class="goods-section clr">
		<div class="section-title">
			<h3>굿즈</h3>
			<div class="page-arrow">
				<LeftArrow class="goods-button-prev" />
				<h5 class="current-page goods-swiper-pagination">1/1</h5>
				<RightArrow class="goods-button-next" />
			</div>
		</div>

		<Swiper
			class="goods-swiper"
			:options="swiperOption"
			:key="swiperOption.slidesPerView"
			:auto-update="true"
			:auto-destroy="true"
			ref="goodsSlider"
		>
			<SwiperSlide v-for="goodsItem in goodsList" :key="goodsItem._id">
				<GoodsItem :goodsItem="goodsItem" :home="true" />
			</SwiperSlide>
		</Swiper>
	</section>
</template>

<script>
import LeftArrow from '@/assets/icon/LeftArrow.vue';
import RightArrow from '@/assets/icon/RightArrow.vue';
import GoodsItem from '@/components/GoodsItem.vue';

export default {
	created() {
		console.log(this.value);
	},
	components: {
		LeftArrow,
		RightArrow,
		GoodsItem,
	},
	props: {
		value: {},
		goodsList: {
			type: Array,
		},
	},
	data() {
		return {
			isLoading: false,
			swiperOption: {
				slidesPerView: this.value,
				slidesPerGroup: this.value,
				spaceBetween: 20,
				pagination: {
					el: '.goods-swiper-pagination',
					type: 'fraction',
				},
				navigation: {
					nextEl: '.goods-button-next',
					prevEl: '.goods-button-prev',
				},
			},
		};
	},
	computed: {},
	methods: {},
	watch: {
		value: function(value) {
			console.log(value);

			this.swiperOption.slidesPerView = value;
			this.swiperOption.slidesPerGroup = value;
		},
	},
};
</script>

<style lang="scss" scoped>
.article-item {
	width: 100%;
}
.swiper-pagination {
	position: static;
}

.swiper-button-disabled path {
	fill: #e6e6e6;
}
</style>
