<template>
	<section class="program-section clr">
		<div class="section-title">
			<h3>프로그램</h3>
			<div class="page-arrow">
				<LeftArrow class="program-button-prev" />
				<h5 class="current-page program-swiper-pagination">1/1</h5>
				<RightArrow class="program-button-next" />
			</div>
		</div>

		<Swiper
			class="program-swiper"
			:options="swiperOption"
			:key="swiperOption.slidesPerView"
			:auto-update="true"
			:auto-destroy="true"
			ref="programSlider"
		>
			<SwiperSlide v-for="programItem in programList" :key="programItem._id">
				<ProgramItem :programItem="programItem" :home="true" />
			</SwiperSlide>
		</Swiper>
	</section>
</template>

<script>
import LeftArrow from '@/assets/icon/LeftArrow.vue';
import RightArrow from '@/assets/icon/RightArrow.vue';
import ProgramItem from '../ProgramItem.vue';

export default {
	created() {
		console.log(this.programList);
	},
	components: {
		LeftArrow,
		RightArrow,
		ProgramItem,
	},
	props: {
		value: {},
		programList: {
			type: Array,
		},
	},
	data() {
		return {
			isLoading: false,
			swiperOption: {
				slidesPerView: this.value,
				slidesPerGroup: this.value,
				spaceBetween: 20,
				pagination: {
					el: '.program-swiper-pagination',
					type: 'fraction',
				},
				navigation: {
					nextEl: '.program-button-next',
					prevEl: '.program-button-prev',
				},
			},
		};
	},
	computed: {},
	methods: {},
	watch: {
		value: function(value) {
			this.swiperOption.slidesPerView = value;
			this.swiperOption.slidesPerGroup = value;
		},
	},
};
</script>

<style lang="scss" scoped>
.article-item {
	width: 100%;
}
.swiper-pagination {
	position: static;
}

.swiper-button-disabled path {
	fill: #e6e6e6;
}
</style>
