<template>
	<section class="column-section">
		<div class="section-title">
			<h3>칼럼</h3>
			<div class="page-arrow">
				<LeftArrow class="column-button-prev" />
				<h5 class="current-page column-swiper-pagination">1/3</h5>
				<RightArrow class="column-button-next" />
			</div>
		</div>

		<Swiper
			class="column-swiper"
			:options="swiperOption"
			:key="swiperOption.slidesPerView"
			:auto-update="true"
			:auto-destroy="true"
			ref="columnSlider"
		>
			<SwiperSlide v-for="columnItem in columnList" :key="columnItem._id">
				<ColumnItem :item="columnItem" />
			</SwiperSlide>
		</Swiper>
	</section>
</template>

<script>
import LeftArrow from '@/assets/icon/LeftArrow.vue';
import RightArrow from '@/assets/icon/RightArrow.vue';
import ColumnItem from '@/views/user/column/components/Column-Item';

export default {
	components: {
		LeftArrow,
		RightArrow,
		ColumnItem,
	},
	props: {
		value: {},
		columnList: {
			type: Array,
		},
	},
	data() {
		return {
			isLoading: false,
			swiperOption: {
				slidesPerView: this.value,
				slidesPerGroup: this.value,
				spaceBetween: 20,
				pagination: {
					el: '.column-swiper-pagination',
					type: 'fraction',
				},
				navigation: {
					nextEl: '.column-button-next',
					prevEl: '.column-button-prev',
				},
			},
		};
	},
	computed: {},
	methods: {},
	watch: {
		value: function(value) {
			this.swiperOption.slidesPerView = value;
			this.swiperOption.slidesPerGroup = value;
		},
	},
};
</script>

<style lang="scss" scoped>
.article-item {
	width: 100%;
}
.swiper-pagination {
	position: static;
}
</style>
