<template>
	<section class="youtube-section clr">
		<div class="section-title">
			<h3>운동친구 유튜브</h3>
			<div class="page-arrow">
				<LeftArrow class="youtube-button-prev" />
				<h5 class="current-page youtube-swiper-pagination">1/3</h5>
				<RightArrow class="youtube-button-next" />
			</div>
		</div>

		<Swiper
			class="youtube-swiper"
			:options="swiperOption"
			:key="swiperOption.slidesPerView"
			:auto-update="true"
			:auto-destroy="true"
			ref="YoutubeSlider"
		>
			<SwiperSlide v-for="youtubeItem in youtubeList" :key="youtubeItem._id">
				<YoutubeItem :item="youtubeItem" />
			</SwiperSlide>
		</Swiper>
	</section>
</template>

<script>
import LeftArrow from '@/assets/icon/LeftArrow.vue';
import RightArrow from '@/assets/icon/RightArrow.vue';
import YoutubeItem from '@/views/user/youtube/components/Youtube-Item';

export default {
	created() {
		console.log(this.youtubeList);
	},
	components: {
		LeftArrow,
		RightArrow,
		YoutubeItem,
	},
	props: {
		value: {},
		youtubeList: {
			type: Array,
		},
	},
	data() {
		return {
			isLoading: false,
			swiperOption: {
				slidesPerView: this.value,
				slidesPerGroup: this.value,
				spaceBetween: 20,
				pagination: {
					el: '.youtube-swiper-pagination',
					type: 'fraction',
				},
				navigation: {
					nextEl: '.youtube-button-next',
					prevEl: '.youtube-button-prev',
				},
			},
		};
	},
	computed: {},
	methods: {},
	watch: {
		value: function(value) {
			this.swiperOption.slidesPerView = value;
			this.swiperOption.slidesPerGroup = value;
		},
	},
};
</script>

<style lang="scss" scoped>
.article-item {
	width: 100%;
}
.swiper-pagination {
	position: static;
}

.swiper-button-disabled path {
	fill: #e6e6e6;
}
</style>
