<template>
	<article class="program-item flex" :class="{ home: home }">
		<div class="thumb">
			<img :src="programItem.thumbnailImgPath" alt="pre" />
		</div>
		<div class="program-info">
			<div class="state">
				<h6>{{ programItem.status == 'INPROGRESS' ? '진행중' : '종료' }}</h6>

				<TitleLine class="line" />
			</div>

			<h4 class="title">{{ programItem.title }}</h4>
			<ul class="program-detail">
				<li>
					<div class="item-icon"><ProgramHost /></div>
					<span class="body2">{{ programItem.coach }}</span>
				</li>
				<li>
					<div class="item-icon"><ProgramDate /></div>
					<span class="body2">{{ programItem.classDate }}</span>
				</li>
				<li>
					<div class="item-icon"><ProgramTime /></div>
					<span class="body2">{{ programItem.classTime }}</span>
				</li>
				<li>
					<div class="item-icon"><ProgramPlace /></div>
					<span class="body2">{{ programItem.classAddress }}</span>
				</li>
			</ul>

			<router-link
				:to="{ name: 'ProgramDetail', params: { id: programItem.id } }"
			>
				<button class="btn">
					자세히보기
				</button>
			</router-link>
		</div>
	</article>
</template>

<script>
import TitleLine from '@/assets/icon/TitleLine';
import ProgramHost from '@/assets/icon/program_goods/ProgramHost';
import ProgramDate from '@/assets/icon/program_goods/ProgramDate';
import ProgramTime from '@/assets/icon/program_goods/ProgramTime';
import ProgramPlace from '@/assets/icon/program_goods/ProgramPlace';

export default {
	components: {
		TitleLine,
		ProgramHost,
		ProgramDate,
		ProgramTime,
		ProgramPlace,
	},
	props: {
		programItem: {
			type: Object,
			required: true,
		},
		home: {
			type: Boolean,
			default: false,
		},
	},
};
</script>

<style>
/* program-item */
.program-item.home {
	width: 100%;
}
.program-item {
	width: calc(50% - 10px);
	height: 320px;
	background: #fafafa;
	float: left;
	margin-right: 10px;
	margin-bottom: 20px;
}
.program-item:nth-of-type(2n) {
	margin-right: 0;
}

.program-item .thumb {
	width: 45%;
	height: 100%;
	overflow: hidden;
	background: gray;
}

.program-info {
	width: 55%;
	padding: 20px;
	box-sizing: border-box;
}

.program-info .title {
	margin-bottom: 10px;
}

.program-detail {
	margin-bottom: 20px;
}

.program-detail li {
	line-height: 24px;
	margin-bottom: 8px;
}

.item-icon {
	display: inline-block;
	width: 18px;
	vertical-align: middle;
	margin-right: 5px;
}

.program-info .state {
	display: inline-block;
	position: relative;
	margin-bottom: 13px;
	line-height: 14px;
}

.program-info .state .line {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	margin-top: -4px;
}

@media screen and (max-width: 768px) {
	.program-item {
		width: 100%;
	}
}
</style>
