<template>
	<div>
		<YoutubeSection v-model="length" :youtubeList="youtubeList" />
		<ProgramSection
			v-if="programList.length > 1"
			:programList="programList"
			v-model="pgLength"
		/>
		<BoardSection :noticeList="noticeList" :newsList="newsList" />
		<GoodsSection
			v-if="goodsList.length > 1"
			:goodsList="goodsList"
			v-model="pgLength"
		/>
		<ColumnSection :columnList="columnList" v-model="length" />
		<LoadingSpinner :loading="isLoading" />
	</div>
</template>

<script>
import BoardSection from '@/components/home/Board-Section.vue';
import ColumnSection from '@/components/home/Column-Section.vue';
import YoutubeSection from '@/components/home/Youtube-Section.vue';
import GoodsSection from '@/components/home/Goods-Section.vue';
import ProgramSection from '@/components/home/Program-Section.vue';
// @ is an alias to /src

import { fetchMain } from '@/api/main';
import LoadingSpinner from '@/components/common/LoadingSpinner';
export default {
	created() {
		window.addEventListener('resize', this.handleResize);
		this.checkWidth();
		this.handleResize();
		this.fetchMain();
	},
	name: 'home',
	components: {
		LoadingSpinner,
		YoutubeSection,
		BoardSection,
		ColumnSection,
		ProgramSection,
		GoodsSection,
	},
	data() {
		return {
			windowWidth: 0,
			length: 3,
			pgLength: 2,
			newsList: [],
			noticeList: [],
			youtubeList: [],
			columnList: [],
			goodsList: [],
			programList: [],
		};
	},
	methods: {
		checkWidth() {
			if (window.innerWidth < 750) {
				this.length = 1;
				this.pgLength = 1;
			} else {
				this.length = 3;
				this.pgLength = 2;
			}
		},
		handleResize() {
			this.windowWidth = window.innerWidth;
		},
		async fetchMain() {
			this.isLoading = true;
			try {
				const { data } = await fetchMain();
				this.youtubeList = data.youtubeList;
				this.columnList = data.columnsList;
				this.noticeList = data.noticeList;
				this.newsList = data.newsList;
				this.goodsList = data.goodsList;
				this.programList = data.programList;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		windowWidth: function(value) {
			if (value < 750) {
				this.length = 1;
				this.pgLength = 1;
			} else {
				this.length = 3;
				this.pgLength = 2;
			}
		},
	},
};
</script>
